.container {
    padding: 1rem 1.5rem;
    display: grid;
    row-gap: 1rem;
    border: 0.1rem solid var(--color-accent);
    border-radius: 1rem;
}

.title {
    font-size: 1.8rem;
    color: var(--color-accent);
    font-weight: 400;
}

.text {
    margin-top: 0.5rem;

    @media (max-width: 600px) {
        font-size: 1.5rem;
    }
}