.container {
    border: 1px solid var(--color-accent);
    border-radius: 1rem;
    padding: 1rem 1.3rem;
    margin: 2rem 0;
    cursor: pointer;

    @media (max-width: 1200px) {
        margin: 0;
        height: max-content;
        border-color: var(--color-sidebar-decoration);
    }
}

.container__inactive {
    border-color: var(--color-sidebar-decoration);
}

.flightNumber {
    font-size: 1.4rem;
    color: var(--color-sidebar-text);
    margin: 0;
}

.name {
    font-size: 1.8rem;
    color: var(--color-text-white);
    line-height: 1.5rem;
    margin: 0 0 0.7rem 0;
}

.flexRow {
    display: flex;
    padding-left: 1rem;
    color: var(--color-sidebar-text);

    p {
        font-size: 1.4rem;
        color: var(--color-sidebar-text);
    }
}

.spacer {
    padding: 0 1.2rem;
    font-size: 1rem;
    color: var(--color-sidebar-decoration) !important;
}
